<template>
  <div>
    <h1>
      小计: {{$store.getters.totalPrice}}
    </h1>
    <h2>
      {{msg}}
    </h2>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        msg: ''
      }
    },
    created() {
      this.$bus.$on('toData', (msg) => {
        this.msg = msg
      })
    },
    computed: {

    }
  }
</script>

<style scoped>

</style>



