<template>
  <div>
    <h1>欢迎来到vue页面</h1>
    <router-link to="/pageTwo">跳转到第二页</router-link>
  </div>
</template>

<script>
  import comA from './comA'
  import comB from './comB'
  import axios from 'axios'
  import jsonp from 'jsonp'
  import {mapMutations} from 'vuex'
  import collapse from './collapse'

  export default {
    data () {
      return {
        count: 0
      }
    },
    components: {
      comA,
      comB,
      collapse
    },
    methods: {
      handleGetData() {
        axios.get('http://localhost:3000/getMsg', {
          withCredentials: true
        }).then(res => {
          console.log(res)
        }).catch(err => {
          console.log('err', err)
        })
      },
      changeCount () {
        this.$store.commit('CHANGE_COUNT', this.count)
      },
      ...mapMutations(['CHANGE_COUNT']),
      getJsonp() {
        let script = document.createElement('script'); // 创建一个script元素
        let body = document.body
        script.src = 'http://localhost:3000/getJsonp?callback=getData'
        body.appendChild(script)
      },
      getJsonp2() {
        jsonp('http://localhost:3000/getJsonp', {
          param: 'callback',
          name: 'getData'
        }, function getData(res) {
          console.log(res)
        })
      },
      getNoSimple() {
        axios.post('http://localhost:3000/noSimple', {username: 'yao'}, {withCredentials: true}).then(res => {
          console.log(res)
        })
      },
      handleEnd () {
        axios.get('/api/getMsg').then(res => {
          console.log(res)
        })
      },
      handleAsync () {
        // setTimeout(() => {
        //   this.CHANGE_COUNT(this.count)
        // }, 1000)
        this.$store.dispatch('handleAsyncAction', 888)
      }
    }
  }
</script>

<style scoped>

</style>



