<template>
  <div class="yao-collapse">
    <div class="yao-collapse-title">
      <div class="title">
        {{title}}
      </div>
      <div class="yao-collapse-btn" @click="toggle">
        {{isCollapse ? '展开': '收起'}}
      </div>
    </div>
    <transition
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
      v-on:after-enter="afterEnter"
      v-on:before-leave="beforeLeave"
      v-on:leave="leave"
      v-on:after-leave="afterLeave"
    >
      <div class="collapse-area" v-show="!isCollapse">
        <slot></slot>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: "collapse",
    props: {
      title: {
        type: String,
        default: '标题传参title'
      }
    },
    data () {
      return {
        isCollapse: true, // 默认不折叠
        height: '',
        duration: 300
      }
    },
    methods: {
      beforeEnter: function (el) {
        // ...进入前
        this.height = 0
        el.style.height = 0
        el.style.transition = 'height .3s ease'
        console.log(el.offsetHeight)
      },
      // 当与 CSS 结合使用时
      // 回调函数 done 是可选的
      enter: function (el, done) {
        // ...进入中
        console.log(el.scrollHeight)
        this.height = el.scrollHeight
        el.style.height = this.height + 'px';
        el.scrollHeight
        setTimeout(() => {
          done()
        }, this.duration)
      },
      afterEnter: function (el) {
        // ...进入后
        el.style.height = ''
        this.height = ''
        el.style.transition = ''
      },
      enterCancelled: function (el) {
        // ...
      },

      // --------
      // 离开时
      // --------

      beforeLeave: function (el) {
        // ...离开前获取高度
        this.height = el.scrollHeight
        el.style.height = this.height + 'px'
        el.style.transition = 'height .3s ease'
        el.scrollHeight

      },
      // 当与 CSS 结合使用时
      // 回调函数 done 是可选的
      leave: function (el, done) {
        // ...离开时，高度赋值为0，执行过渡动画
        el.style.height = 0
        setTimeout(() => {
          done()
        }, this.duration)
      },
      afterLeave: function (el) {
        // ...离开后，清除样式

        el.style.height = ''
        el.style.transition = ''
      },
      toggle () {
        this.isCollapse = !this.isCollapse
      }
    }
  }
</script>

<style scoped>
  .yao-collapse-title {
    display: flex;
    justify-content: space-between;
    min-width: 375px;
    padding: 20px;
  }

  .collapse-area {
    overflow: hidden;
  }
</style>



